.quality {
	margin-bottom: rem(200);

	height: rem(1280);

	@include min1240 {
		height: auto;
	}

	@include min768 {
		margin-bottom: rem(150);
	}

	@include min540 {
		margin-bottom: rem(20);
	}

	&__top {
		margin-bottom: rem(90);
		position: relative;
		z-index: 2;

		@include min {
			margin-bottom: 0;
		}

		@include min380 {
			.sect-title-40 {
				font-size: rem(20);
			}
		}
	}

	&__bottom {
		position: relative;

		height: rem(1495);

		@include min1240 {
			height: rem(1064);
		}

		@include min {
			height: rem(1014);
		}

		@include min768 {
			height: rem(774);
		}

		@include min540 {
			height: rem(390);
		}

		img {
			position: absolute;
			top: rem(-400);
			left: 0;
			width: 100%;
			// height: 100%;
			height: rem(1495);
			display: block;
			// background-position: top center;
			object-fit: cover;
			object-position: top center;

			@include min {
				top: rem(-240);
				height: rem(1256);
			}

			@include min768 {
				top: rem(-465);
			}

			@include min540 {
				top: rem(-240);
				height: rem(640);
			}
		}

		&_wrap {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			display: flex;
			justify-content: center;

			@include min768 {
				position: relative;

				margin-top: rem(64);
			}

			.item {
				position: absolute;
				z-index: 10;
				background-color: rgba(40, 40, 46, .96);
				padding: rem(40) rem(50);

				@include min768 {
					position: relative;
				}

				// transition: var(--transition);

				@include max768 {
					&:hover {
						z-index: 11;
						background-color: #34343D;

						width: rem(413);

						.item__image {
							.line {
								opacity: 1;
							}
						}

						.item__descr {
							display: block;
						}
					}
				}

				

				&__image {
					position: absolute;

					@include min540 {
						svg {
							width: rem(32) !important;
							height: rem(32) !important;
						}
					}

					.line {
						opacity: 0;
					}
				}

				&__title {

					font-size: rem(24);
					line-height: 120%;
					color: white;

					@include min540 {
						font-size: rem(14);
						white-space: nowrap;
					}
				}

				&__descr {
					font-size: rem(16);
					line-height: 140%;
					color: #8E8E93;
					margin-top: rem(15);
					display: none;

					transition: var(--transition);
				}
			}

			.item.active {

				.item__image {
					.line {
						opacity: 1;
					}
				}

				.item__descr {

					@include max768 {
						display: block;
					}
				}
			}
			.item_1 {
				top: rem(65);
				right: rem(840);

				width: rem(256);

				@include min1240 {
					top: rem(58);
					right: rem(540);
				}

				@include min768 {
					top: auto;
					right: auto;
					margin-bottom: rem(28);
					padding: rem(30) rem(50);
				}

				@include min540 {
					width: auto;
					align-self: flex-start;
					margin-left: rem(32);
					padding: rem(16) rem(30) rem(16) rem(24);
				}

				.item__image {
					top: rem(20);
					right: rem(-32);

					@include min540 {
						top: rem(8);
						right: rem(-16);
					}
				}
			}

			.item_2 {
				top: rem(142);
				left: rem(727);
				padding: rem(30) rem(50);

				width: rem(244);

				@include min1240 {
					left: rem(564);
				}

				@include min768 {
					top: auto;
					left: auto;


					align-self: flex-end;
					margin-right: rem(10);
					margin-bottom: rem(38);
				}

				@include min540 {
					width: auto;
					margin-right: rem(32);
					padding: rem(16) rem(24) rem(16) rem(30);
				}

				.item__image {
					top: rem(10);
					left: rem(-32);

					@include min540 {
						top: rem(8);
						left: rem(-16);
					}
				}
			}

			.item_3 {
				top: rem(382);
				left: rem(238);
				padding: rem(30) rem(50);

				width: rem(380);

				@include min768 {
					top: auto;
					left: auto;

					margin-left: rem(46);
					margin-bottom: rem(28);
				}

				@include min540 {
					width: auto;
					align-self: flex-start;
					margin-left: rem(85);
					padding: rem(16) rem(24) rem(16) rem(30);
				}

				.item__image {
					top: rem(10);
					left: rem(-32);

					@include min540 {
						top: rem(8);
						left: rem(-16);
					}
				}
			}

			.item_4 {
				top: rem(520);
				left: rem(496);
				padding: rem(30) rem(50);

				width: rem(413);

				@include min1240 {
					left: rem(320);
				}

				@include min768 {
					top: auto;
					left: auto;
					width: rem(278);
					align-self: flex-end;
				}

				@include min540 {
					width: auto;
					align-self: flex-end;
					margin-right: rem(32);
					padding: rem(16) rem(30) rem(16) rem(24);
				}

				.item__image {
					top: rem(10);
					right: rem(-32);

					@include min768 {
						right: auto;
						left: rem(-32);
					}

					@include min540 {
						top: rem(8);
						left: rem(-16);
					}
				}
			}
		}

		.container {
			position: relative;
			width: 100%;
			// height: 100%;

			@include min768 {
				display: flex;
				flex-direction: column;

				margin-left: rem(32);
				margin-right: rem(32);
			}
		}

	}

	&__bottom_mobile {
		display: none;

		@include min768 {
			display: flex;
			flex-direction: column;

			padding: rem(40) rem(32);

			.title {
				font-size: rem(20);
				line-height: 140%;
				color: var(--c-accent);
				margin-bottom: rem(10);
			}

		}
	}
}