.info {
	position: relative;
	margin-bottom: rem(270);

	@include min1240 {
		margin-bottom: rem(82);
	}

	@include min768 {
		margin-bottom: rem(50);
	}

	&::before {
		content: "";
		position: absolute;
		bottom: rem(-342);
		left: 0;
		background: url(../img/section2/left_bg.png) no-repeat top left;
		background-size: cover;
		width: rem(285);
		height: rem(757);
		z-index: -1;

		@include min1440 {

			left: rem(-120);
		// 	width: 100%;
		// 	height: rem(320);
		}

		@include min540 {
			background: url(../img/section2/left_bg_mobile.png) no-repeat top left;
			background-size: cover;
			left: auto;
			right: 0;
			bottom: rem(-82);
			width: rem(108);
			height: rem(435);
			z-index: 1;
		}
	}

	> .container {
		display: flex;
		flex-wrap: wrap;
		position: relative;

		@include min540 {
			width: 100%;
		}
	}

	&__left {
		display: flex;
		flex-direction: column;

		flex: 0 0 rem(410);
		max-width: rem(410);
		margin-right: rem(112);

		@include min1240 {
			margin-right: 0;
			flex: 0 0 100%;
			max-width: 100%;
		}

		@include min540 {
			margin-left: rem(32);
			margin-right: rem(32);
			flex: 0 0 calc(100% - 64px);
			max-width: calc(100% - 64px);
		}

		.subtitle {
			color: #878787;
			font-size: rem(12);
			letter-spacing: rem(2);
			line-height: 140%;
			text-transform: uppercase;
			margin-bottom: rem(44);

			@include min768 {
				margin-bottom: rem(12);
			}

			@include min540 {
				font-size: rem(11);
			}
		}

		.sect-title-40 {
			margin-bottom: rem(40);

			@include min768 {
				margin-bottom: rem(24);
			}
		}

		.sect-text {
			margin-bottom: rem(40);

			@include min768 {
				margin-bottom: rem(24);
			}

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	&__right {
		flex: 0 0 rem(870);
		max-width: rem(870);

		margin-right: rem(-200);

		@include min1240 {
			margin-right: 0;
			flex: 0 0 100%;
			max-width: 100%;
			margin-top: rem(48);

			display: flex;
			justify-content: center;
		}

		

	}

	&__items {
		position: absolute;
		bottom: rem(-100);
		right: rem(194);

		padding: rem(40) rem(90);

		display: flex;
		flex-wrap: wrap;
		background-color: white;
		box-shadow: 0px 22px 39px -26px rgba(199, 211, 217, 0.5);

		@include min1240 {
			position: relative;
			right: auto;
			bottom: rem(45);
			margin-left: auto;
			margin-right: auto;
		}

		@include min768 {
			padding: rem(30) rem(60);
		}

		@include min540 {
			flex: 0 0 rem(312);
			max-width: rem(312);
		}

		@include min340 {
			padding: rem(30) rem(40);
			flex: 0 0 rem(270);
			max-width: rem(270);
		}

		.item {
			display: flex;
			flex-direction: column;

			margin-right: rem(72);

			@include min768 {
				margin-right: rem(42);
			}

			&:first-child {
				@include min540 {
					order: -9999;
				}
			}
			
			&:nth-child(2) {
				@include min540 {
					order: 9999;
					flex: 0 0 100%;
					max-width: 100%;
					
				}
			}

			&:last-child {
				margin-right: 0;

				@include min540 {
					order: 1;
					margin-bottom: rem(30);
				}
			}

			span {
				&:last-child {
					line-height: 140%;
					color: #B9B9B9;
					text-transform: lowercase;
				}
			}
		}
	}
}