.info_company {

	margin-bottom: rem(192);

	@include min768 {
		margin-bottom: rem(112);
	}

	> .container {
		display: flex;
		flex-direction: column;
	}

	&__top {
		display: flex;
		flex-wrap: wrap;

		margin-bottom: rem(150);

		@include min1050 {
			margin-bottom: rem(50);
		}

		&_left {
			margin-right: rem(75);
			flex: 0 0 rem(223);
			max-width: rem(223);

			@include min768 {
				flex: 0 0 rem(124);
				max-width: rem(124);
				margin-right: rem(24);

				svg {
					width: 100% !important;
					height: 100% !important;
				}
			}

			@include min540 {
				display: none;
			}
		}

		&_right {
			display: flex;
			flex-direction: column;

			flex: 1;

			@include min540 {
				.flex {
					margin-bottom: rem(24);
					p {
						flex: 1;
					}
				}
			}

			.image-mobile {
				display: none;

				@include min540 {
					display: inline-block;
					float: left;

					width: rem(74);
					height: rem(90);
					margin-right: rem(34);

					img {
						width: rem(74);
						height: rem(90);
					}
				}
			}

			.sect-title-40 {
				margin-bottom: rem(35);

				@include min768 {
					margin-bottom: rem(24);
				}
			}

			.sect-text {
				margin-bottom: rem(28);

				@include min768 {
					margin-bottom: rem(24);
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;

		.item {
			display: flex;
			flex-direction: column;
			align-items: center;

			@include min768 {
				flex-direction: row;
			}
			

			&__image {
				width: rem(75);
				height: rem(75);
				border-radius: 50%;
				background-color: #E2DBD0;

				position: relative;

				margin-bottom: rem(20);

				@include min768 {
					flex: 0 0 rem(66);
					width: rem(66);
					height: rem(66);
					margin-bottom: 0;

					margin-right: rem(32);
				}

				svg {
					position: absolute;
					top: 0;
					right: 0;
				}
			}

			&__text {
				text-align: center;
				font-size: rem(18);
				line-height: 130%;
				color: var(--c-text-black);

				@include min768 {
					text-align: left;
					font-size: rem(16);
				}
			}

			&:first-child {
				flex: 0 0 rem(210);
				max-width: rem(210);

				margin-right: rem(44);

				@include min1200 {
					flex: 0 0 calc((100% - 96px) / 4);
					max-width: calc((100% - 96px) / 4);
					margin-right: rem(24);
				}

				@include min {
					flex: 0 0 calc((100% - 48px) / 2);
					max-width: calc((100% - 48px) / 2);

					margin-bottom: rem(24);
				}

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: rem(36);
				}

				.item__image {
					svg {
						top: rem(14);
						right: rem(4);
					}
				}
			}

			&:nth-child(2) {
				flex: 0 0 rem(303);
				max-width: rem(303);

				margin-right: rem(14);

				@include min1200 {
					flex: 0 0 calc((100% - 96px) / 4);
					max-width: calc((100% - 96px) / 4);
					margin-right: rem(24);
				}

				@include min {
					flex: 0 0 calc((100% - 48px) / 2);
					max-width: calc((100% - 48px) / 2);

					margin-bottom: rem(24);
				}

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: rem(36);
				}

				.item__image {
					svg {
						top: rem(12);
					}
				}
			}

			&:nth-child(3) {
				flex: 0 0 rem(269);
				max-width: rem(269);

				margin-right: rem(77);

				@include min1200 {
					flex: 0 0 calc((100% - 96px) / 4);
					max-width: calc((100% - 96px) / 4);
					margin-right: rem(24);
				}

				@include min {
					flex: 0 0 calc((100% - 48px) / 2);
					max-width: calc((100% - 48px) / 2);
				}

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: rem(36);
				}

				.item__image {
					svg {
						top: rem(12);
					}
				}
			}

			&:last-child {
				margin-right: 0;
				flex: 0 0 rem(176);
				max-width: rem(176);

				@include min1200 {
					flex: 0 0 calc((100% - 96px) / 4);
					max-width: calc((100% - 96px) / 4);
					margin-right: rem(24);
				}

				@include min {
					flex: 0 0 calc((100% - 48px) / 2);
					max-width: calc((100% - 48px) / 2);
				}

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: rem(36);
				}

				.item__image {
					svg {
						top: rem(12);
					}
				}
			}
		}
	}

}