.layouts {
	position: relative;
	padding-bottom: rem(250);

	background-color: white;

	@include min {
		padding-bottom: rem(120);
	}

	&::before, &::after {
		content: "";
		position: absolute;
		z-index: 0;
		top: rem(-120);

		@include min1050 {
			top: rem(-80);
		}

		@include min {
			display: none;
		}
	}

	&::before {
		left: 0;
		width: rem(825);
		height: rem(744);
		background: url(../img/section6/left_bg.png) no-repeat;
	}

	&::after {
		right: rem(-540);
		width: rem(825);
		height: rem(744);
		background: url(../img/section6/right_bg.png) no-repeat;
	}

	&__top {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		margin-bottom: rem(52);

		@include min {
			flex-direction: column;
			align-items: flex-start;
			margin-bottom: rem(40);
		}

		.sect-title-40 {
			margin-right: rem(120);

			@include min {
				margin-right: 0;
				margin-bottom: rem(20);
			}
		}

		
	}

	&__bottom {
		position: relative;
		z-index: 2;
		display: flex;
		flex-wrap: wrap;

		@include min {
			justify-content: space-between;

			flex-direction: column;
		}

		&_left {
			flex: 0 0 rem(700);
			max-width: rem(700);

			@include min1200 {
				flex: 0 0 50%;
				max-width: 50%;
			}

			@include min {
				position: absolute;
				top: rem(170);
				left: 0;
				flex: 0 0 rem(540);
				max-width: rem(540);

				order: 9999;
			}

			@include min540 {
				top: rem(200);
				flex: 0 0 rem(310);
				max-width: rem(310);
			}

			@include min350 {
				top: rem(230);
			}

			.layouts_tab_wrap {
				display: none;
			}
			.layouts_tab_wrap.active {
				display: block;
			}
		}

		&_right {
			margin-left: rem(100);

			flex: 0 0 rem(310);
			max-width: rem(310);

			@include min1050 {
				margin-left: rem(32);
			}

			@include min {
				flex: 0 0 100%;
				max-width: 100%;
				margin-left: 0;

				order: -9999;
			}

			@include min540 {
				.button-accent {
					width: 100%;
				}
			}

			.title {
				font-size: rem(36);
				line-height: 140%;
				color: var(--c-text-black);
				margin-bottom: rem(24);

				@include min {
					font-size: rem(24);
					margin-bottom: rem(20);
				}

				span {
					color: var(--c-accent);
				}
			}

			.sect-text {
				margin-bottom: rem(24);

				

				&:last-of-type {
					margin-bottom: 0;


					@include min {
						margin-bottom: rem(500);
					}

					@include min540 {
						margin-bottom: rem(320);
					}
				}
			}

			.items {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				margin-top: rem(64);
				margin-bottom: rem(74);

				@include min540 {
					margin-bottom: rem(48);
				}

				.item {
					display: flex;
					// flex-wrap: wrap;
					align-items: center;
					width: calc((100% - 75px) / 2);

					margin-bottom: rem(46);

					&:nth-last-child(2), &:last-child {
						margin-bottom: 0;
					}

					&__image {
						flex: 0 0 rem(23);
						max-width: rem(23);
						margin-right: rem(22);
						display: flex;
						justify-content: center;

						@include min540 {
							flex: 0 0 rem(18);
							max-width: rem(18);
							margin-right: rem(16);
						}
					}

					span {
						line-height: 140%;
						color: var(--c-text-black);
						white-space: nowrap;

						@include min540 {
							font-size: rem(14);
						}

						b {
							font-size: rem(24);
							margin-right: rem(3);
							font-weight: normal;

							@include min540 {
								font-size: rem(20);
							}
						}
					}
				}
			}
		}
	}
}