.gallery {
	margin-bottom: rem(64);
	background-color: white;

	@include min {
		margin-bottom: rem(50);
	}

	&__top {
		margin-bottom: rem(50);

		@include min {
			margin-bottom: rem(32);
		}

		.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include min {
				.sect-title-40 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: rem(40);
				}
			}

			.sect-tab-button {
				margin-left: rem(140);

				@include min {
					margin-left: 0;
				}
			}

			.button-play {
				margin-left: auto;

				display: flex;
				align-items: center;

				@include min540 {
					flex-direction: row-reverse;
				}
				

				&:hover {
					.text {
						color: var(--c-accent);
					}

					.icon {
						background-color: var(--c-accent);
					}
				}

				.text {
					font-size: rem(12);
					text-transform: uppercase;
					color: var(--c-text-black);

					transition: var(--transition);

					@include min540 {
						max-width: rem(64);
						text-align: left;
					}

					@include min380 {
						font-size: rem(10);
						max-width: rem(52);
					}
				}

				.icon {
					margin-left: rem(27);
					background-color: var(--c-text-black);
					width: rem(52);
					height: rem(52);
					border-radius: 50%;

					display: flex;
					align-items: center;
					justify-content: center;

					transition: var(--transition);

					@include min540 {
						margin-left: 0;
						margin-right: rem(12);

						width: rem(34);
						height: rem(34);

						svg {
							width: rem(5) !important;
							height: rem(6.8) !important;
						}
					}

					@include min380 {
						margin-right: rem(6);
					}
				}
			}
		}
	}

	&__bottom {
		max-height: rem(800);
		position: relative;

		@include min768 {
			max-height: rem(310);
		}

		.swiper {
			height: rem(800);

			@include min768 {
				height: rem(310);
			}

			.swiper-slide {
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}

		.swiper-nav-wrap {
			position: absolute;
			top: 50%;
			left: 0;
			margin-top: rem(-50);
			width: 100%;

			display: flex;
			justify-content: center;

			@include min768 {
				margin-top: rem(-36);
			}

			.container {
				position: relative;
				// z-index: 10;
				width: 100%;
				height: rem(100);

				display: flex;
				align-items: center;

				@include min768 {
					height: rem(73);
					width: calc(100% - 36px);
				}
			}
		}

		.swiper-nav {
			// position: absolute;
			// top: 50%;
			// z-index: 10;
			// left: 0;

			display: flex;
			justify-content: space-between;
			width: 100%;

			.swiper-button-disabled {
				opacity: 0;
			}

			button {
				position: relative;
				z-index: 10;
				width: rem(100);
				height: rem(100);
				border-radius: 50%;
				background-color: rgba(255,255,255,.3);

				span {
					@include max768 {
						display: none;
					}

					@include min768 {
						position: absolute;
						top: rem(-42);
						font-size: rem(12);
						letter-spacing: rem(2);
						text-transform: uppercase;
						line-height: 140%;
						color: rgba(255,255,255,.8);
					}
				}

				@include min768 {
					width: rem(30);
					height: rem(30);

					svg {
						width: rem(21) !important;
						height: rem(5) !important;

						path {
							stroke-width: rem(4);
						}
					}
				}

				&:hover {
					background-color: rgba(255,255,255,.45);
				}
			}

			&-next {

				span {
					@include min768 {
						text-align: right;
						left: rem(-26);
					}
				}

				svg {
					position: absolute;
					top: 50%;
					margin-top: rem(-10);
					left: rem(-17);

					@include min768 {
						left: rem(-6);
						margin-top: rem(-2);
					}
				}
			}

			&-prev {

				span {
					@include min768 {
						text-align: left;
						right: rem(-28);
					}
				}

				svg {
					position: absolute;
					top: 50%;
					margin-top: rem(-10);
					right: rem(-17);

					transform: rotate(-180deg);

					@include min768 {
						right: rem(-6);
						margin-top: rem(-2);
					}
				}
			}
		}
	}

	.gallery_tab_wrap {
		display: none;
	}

	.gallery_tab_wrap.active {
		display: block;
	}
}