.modal_menu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #303037;

	padding: rem(32);

	overflow-y: auto;

	z-index: 100;

	display: none;
	flex-direction: column;

	&__close {
		position: absolute;
		top: rem(32);
		left: rem(32);
	}

	&__list {
		margin-top: rem(35);
		margin-bottom: rem(60);

		ul {
			display: flex;
			flex-direction: column;
			align-items: center;

			@include min340 {
				align-items: flex-start;
			}

			li {
				margin-bottom: rem(40);

				@include min340 {
					margin-bottom: rem(20);
				}

				&:last-child {
					margin-bottom: 0;
				}

				a {
					display: flex;
					align-items: center;
					justify-content: center;

					font-size: rem(18);

					
					
					color: white;
					text-transform: uppercase;

					@include min340 {
						justify-content: flex-start;
						font-size: rem(12);
					}

					svg {
						display: none;
					}
				}

				a.active {
					color: var(--c-accent);

					svg {
						@include min340 {
							display: block;
							margin-right: rem(12);
						}
						
					}
				}
			}
		}
	}

	&__bottom {
		a {
			display: flex;
			justify-content: center;
			align-items: center;

			@include min340 {
				justify-content: flex-start;
			}

			.icon {
				svg {
					width: rem(24);
					height: rem(24);

					@include min340 {
						width: rem(16);
						height: rem(16);
					}
				}
			}

			span {
				margin-left: rem(9);
				font-size: rem(18);
				color: white;
				border-bottom: rem(1) solid white;
				letter-spacing: rem(2);

				@include min340 {
					font-size: rem(12);
				}
			}
		}
	}
}

.modal_menu.active {
	display: flex;
}