.header {
	position: relative;
	height: rem(920);

	margin-bottom: rem(150);

	@include min768 {
		height: rem(740);

		margin-bottom: rem(90);
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: url(../img/header_bg.jpg) no-repeat top center;
		background-size: cover;
		clip-path: polygon(0% 0%, 100% 0, 100% 91%, 49% 100%, 0 91%);
		z-index: -1;

		@include min768 {
			clip-path: polygon(0% 0%, 100% 0, 100% 96%, 49% 100%, 0 96%);
		}

		@include min540 {
			background: url(../img/header_bg_mobile.jpg) no-repeat top center;
			background-size: cover;
		}

		@include ratio2 {
			background: url(../img/header_bg_mobile_2x.jpg) no-repeat top center;
			background-size: cover;
		}
	}

	&__buffer {
		display: none;
	}

	&__gradient {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: rem(920);
		background: linear-gradient(180deg, #1E2139 3.83%, rgba(30, 33, 61, 0) 33.05%, rgba(196, 196, 196, 0) 67.94%);
		z-index: 0;
		opacity: .3;

		@include min768 {
			height: rem(740);
		}
	}

	// > .container {
	// 	display: flex;
	// 	flex-direction: column;
	// }

	&__top {
		position: relative;
		z-index: 2;

		padding-top: rem(45);
		padding-bottom: rem(75);

		@include min1050 {
			padding-top: rem(32);
		}

		> .container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
		}

		.logo-fixed {
			display: none;
		}

		.burger {
			display: none;

			@include min1050 {
				display: flex;
				flex-direction: column;
				justify-content: space-between;

				flex: 0 0 rem(20);
				width: rem(20);
				height: rem(16);
				
				.line {
					width: 100%;
					height: rem(2);
					background-color: white;
					border-radius: rem(1);

					&:nth-child(2) {
						width: rem(12);
					}
				}
			}
		}

		&_nav {
			padding-bottom: rem(20);

			@include min1050 {
				display: none;
			}

			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					margin-right: rem(40);

					@include min1200 {
						margin-right: rem(20);
					}

					&:last-child {
						margin-right: 0;
					}

					a {
						font-size: rem(12);
						line-height: 140%;
						color: white;
						letter-spacing: rem(1.5);
						text-transform: uppercase;

						&:hover {
							color: var(--c-accent);
						}
					}
				}

				li.active {
					a {
						position: relative;
						// color: var(--c-accent);

						&::before {
							content: "";
							position: absolute;
							bottom: rem(-16);
							left: 0;
							width: 100%;
							height: rem(2);
							background-color: var(--c-accent);
						}
					}
				}
			}
		}

		&_contact {
			position: relative;
			display: flex;
			flex-direction: column;

			a {
				font-weight: 500;
				color: white;
			}

			button {
				font-size: rem(12);
				color: white;
				text-transform: lowercase;
				text-decoration: underline;

				&:hover {
					color: var(--c-accent);
				}
			}

			.bs-overlay {
				position: absolute;
				top: 0;
				margin-top: rem(-24);
				margin-left: rem(-32);
				left: 0;
				background: url(../img/bs-overlay-phone.svg) no-repeat;
				background-size: cover;

				width: rem(195);
				height: rem(97);

				z-index: -1;
			}
		}
	}

	&__bottom {
		position: relative;
		z-index: 2;
		// display: flex;
		// flex-direction: column;
		// align-items: center;

		> .container {
			display: flex;
			flex-direction: column;
			align-items: center;
		}

		.logo {
			margin-bottom: rem(40);

			@include min540 {
				width: rem(84);
			}
		}

		.title {
			position: relative;
			font-size: rem(100);
			line-height: 125%;
			letter-spacing: rem(10);
			color: white;
			text-transform: uppercase;
			margin-bottom: rem(5);

			@include min768 {
				font-size: rem(72);
			}

			@include min540 {
				font-size: rem(45);
			}

			@include min360 {
				font-size: rem(34);
			}

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: rem(972);
				margin-left: rem(-170);
				height: rem(252);
				margin-top: rem(-42);
				filter: blur(50px);
				background: url(../img/bs-overlay-title.svg) no-repeat;
				z-index: -1;
			}
		}

		.subtitle {
			font-size: rem(18);
			line-height: 140%;
			color: white;
			text-transform: uppercase;
			letter-spacing: rem(2);

			margin-bottom: rem(80);

			@include min540 {
				font-size: rem(13);
				margin-bottom: rem(100);
			}

		}

		.button-play {

			svg {
				width: rem(135);
				height: rem(135);
			}

			@include min540 {
				width: rem(100);
				height: rem(100);

				svg {
					width: rem(100);
					height: rem(100);
				}
			}

			.line2 {
				display: none;
			}

			&:hover {
				.line {
					display: none;
				}
				.line2 {
					display: block;
				}
			}
		}


	}

}

.header.header-fixed {

	position: relative;
	z-index: 100;
	
	&::after {
		content: "";
		position: fixed;
		top: 0;
		left: 0;
		height: rem(80);
		width: 100%;
		background-color: rgba(255,255,255,.9);
		
		z-index: 98;

		box-shadow: 0px 22px 39px -29px rgba(199, 211, 217, 0.5);
	}

	.header__buffer {
		display: block;
		width: 100%;
		height: rem(163);
	}

	.header__top {
		position: fixed;
		z-index: 10;
		top: 0;
		left: 0;
		width: 100%;

		z-index: 99;

		height: rem(80);
		padding-top: 0;
		padding-bottom: 0;

		display: flex;
		align-items: center;

		>.container {
			width: 100%;

			@include min1200 {
				width: calc(100% - 64px);
			}
		}

		.logo-fixed {
			display: block;

			@include min1200 {
				display: none;
			}
		}

		.burger {
			@include min1200 {
				.line {
					background-color: var(--c-text-black);
				}
			}
		}

		&_nav {
			padding-bottom: 0;

			ul {

				li {

					a {
						color: var(--c-text-black);

						&:hover {
							color: var(--c-accent);
						}
					}

					a.active {
						position: relative;

						&::before {
							content: "";
							position: absolute;
							bottom: rem(-29);
							left: 0;
							width: 100%;
							height: rem(2);
							background-color: var(--c-accent);
						}
					}
				}

				li.active {
					a {
						position: relative;

						&::before {
							content: "";
							position: absolute;
							bottom: rem(-29);
							left: 0;
							width: 100%;
							height: rem(2);
							background-color: var(--c-accent);
						}
					}
				}
			}
		}

		&_contact {
			.bs-overlay {
				display: none;
			}

			a {
				font-weight: 500;
				color: var(--c-text-black);
			}

			button {
				color: var(--c-text-black);

				&:hover {
					color: var(--c-accent);
				}
			}
		}
	}

}