.map {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(230);

	@include min768 {
		margin-bottom: rem(70);
	}

	&__top {
		margin-bottom: rem(60);

		@include min768 {
			margin-bottom: 0;
		}

		.container {
			display: flex;
			flex-wrap: wrap;
			padding: 0 rem(80);

			@include min1050 {
				padding: 0;
			}
		}

		.item {
			cursor: pointer;
			display: flex;
			flex-direction: column;

			flex: 1;

			padding: 0 rem(50);

			border-right: rem(1) solid rgba(86, 86, 92, .2);

			@include min1050 {
				padding: 0 rem(24);
			}

			@include min768 {
				flex: 0 0 50%;
				max-width: 50%;
				margin-bottom: rem(50);

				&:first-child {
					padding-left: 0;
				}

				&:nth-child(3) {
					padding-left: 0;
				}

				&:nth-child(2n) {
					border-right: 0;
					padding-right: 0;
				}
			}

			&:last-child {
				border-right: 0;
			}

			&:hover {
				.item__top {
					color: var(--c-accent);
				}

				.item__bottom {
					color: var(--c-text-black);
				}
			}

			&__top {
				line-height: 140%;
				color: var(--c-text-black);
				font-size: rem(24);
				margin-bottom: rem(4);

				transition: var(--transition);

				@include min540 {
					font-size: rem(20);
				}

				span {
					font-size: rem(40);

					@include min540 {
						font-size: rem(32);
					}
				}
			}

			&__bottom {
				font-size: rem(14);
				line-height: 120%;
				color: #8E8E93;
				transition: var(--transition);

				@include min540 {
					font-size: rem(13);
				}
			}
		}
	}

	&__bottom {
		position: relative;

		height: rem(670);

		@include min1200 {
			// width: rem(1200);
			overflow-x: auto;
		}
		
		// @include min {
		// 	height: rem(1374);
		// }

		@include min540 {
			overflow-x: hidden;
			height: rem(430);
		}

		img {
			width: 100%;
			height: 100%;
			display: block;
			object-fit: cover;

			@include min1200 {
				min-width: rem(1200);
			}

			@include min540 {
				min-width: 100%;
			}
		}

		&_wrap {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			display: flex;
			justify-content: center;

			@include min1200 {
				width: rem(1200);
			}

			@include min768 {
				width: 100%;
			}
		}

		.container {
			position: relative;
			width: 100%;
			// height: 100%;

			@include min1200 {
				width: rem(1200);
			}
		}

		.item {
			position: absolute;
			z-index: 10;
			box-shadow: 0px 22px 39px -26px rgba(199, 211, 217, 0.5);
			background-color: white;
			padding: rem(40) rem(50);
			border: rem(1) solid white;

			transition: var(--transition);

			@include min540 {
				padding: rem(16) rem(24);
				border: 0;
			}

			&:hover {

				@include max768 {
					z-index: 11;
					border-color: rgba(168, 144, 109, .15);
					// background-color: #34343D;

					width: rem(413);

					.item__image {
						.line {
							opacity: 1;
						}
					}

					.item__descr {
						display: block;
					}
				}

				
			}

			&__image {
				position: absolute;

				svg {
					@include min540 {
						width: rem(32) !important;
						height: rem(32) !important;
					}
				}

				.line {
					opacity: 0;
				}
			}

			&__title {
				
				font-size: rem(18);
				line-height: 120%;
				color: var(--c-text-black);

				@include min540 {
					font-size: rem(14);
				}
			}

			&__descr {
				font-size: rem(14);
				line-height: 120%;
				color: #8E8E93;
				margin-top: rem(15);
				display: none;
			}
		}

		.item.active {

			@include max540 {
				width: rem(413);
				z-index: 11;

				.item__image {
					.line {
						opacity: 1;
					}
				}

				.item__descr {
					display: block;
				}
			}

			@include min540 {

				background-color: rgba(40,40,46,.94);

				.item__title {
					color: white;
				}

				.item__image {
					.line {
						opacity: 1;
					}
				}
			}

			
		}

		.item_1 {
			top: rem(470);
			right: rem(774);
			// right: 20%;

			width: rem(250);

			@include min540 {
				top: rem(313);
				right: rem(142);
				width: auto;
			}

			.item__image {
				top: rem(20);
				right: rem(-32);

				@include min540 {
					top: rem(8);
					left: rem(-16);
				}
			}
		}

		.item_2 {
			top: rem(38);
			left: rem(815);
			padding: rem(30) rem(50);

			width: rem(294);

			@include min540 {
				padding: rem(16) rem(24);
				width: auto;
				top: rem(32);
				left: auto;
				right: rem(47);
			}

			.item__image {
				top: rem(5);
				left: rem(-32);

				@include min540 {
					top: rem(8);
					left: auto;
					right: rem(-16);
				}
			}
		}

		.item_3 {
			top: rem(317);
			left: rem(538);
			padding: rem(30) rem(50);

			width: rem(186);

			@include min540 {
				padding: rem(16) rem(24);
				width: auto;
				top: rem(206);
				left: rem(142);
			}

			.item__image {
				top: rem(5);
				left: rem(-32);

				@include min540 {
					top: rem(8);
					left: rem(-16);
				}
			}
		}

		.item_4 {
			top: rem(554);
			left: rem(496);
			padding: rem(30) rem(50);

			width: rem(256);

			@include min540 {
				padding: rem(16) rem(24);
				width: auto;
				top: rem(366);
				left: rem(127);
			}

			.item__image {
				top: rem(5);
				left: rem(-32);

				@include min540 {
					top: rem(8);
					left: rem(-16);
				}
			}
		}
	}
}