.button-accent {
	display: flex;
	align-items: center;
	justify-content: center;

	background-color: var(--c-accent);
	color: white;
	font-size: rem(12);
	font-weight: 600;
	line-height: 140%;
	padding: rem(25) rem(65);
	text-transform: uppercase;

	@include min540 {
		padding: rem(25) rem(48);
	}

	@include min340 {
		padding: rem(25) rem(32);
	}

	&:hover {
		background-color: #BB9C6F;
	}
}

.sect-tab-button {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	.item {
		position: relative;
		margin-right: rem(56);
		font-size: rem(12);
		letter-spacing: rem(2);
		line-height: 140%;
		color: var(--c-text-black);
		text-transform: uppercase;

		&:last-child {
			margin-right: 0;
		}

		@include min540 {
			margin-right: rem(30);
		}

		@include min360 {
			margin-right: rem(12);
		}

		&::before {
			content: "";
			position: absolute;
			bottom: rem(-10);
			left: 0;
			width: 0;
			height: rem(1);
			background-color: var(--c-accent);
			opacity: 0;

			transition: 500ms ease;
		}

		&:hover {
			color: var(--c-accent);
		}
	}

	.item.active {
		color: var(--c-accent);

		&::before {
			width: 100%;
			opacity: 1;
		}
	}
}