.form_present {
	position: relative;
	width: 100%;
	// overflow: hidden;

	// height: rem(543);
	// background: url(../img/section5/message_bg.png) no-repeat top center;
	background-size: cover;
	background: linear-gradient(180deg, rgba(254, 255, 255, 0) 0%, white 100%), url(../img/section5/message_bg.png) no-repeat top center;
	// margin-bottom: rem(248);
	padding-bottom: rem(210);

	@include min1050 {
		background:
		url(../img/section5/message_bg_mobile2x.png) no-repeat bottom center;
		background-size: cover;

		padding-bottom: rem(105);
	}

	&::before {
		content: "";
		position: absolute;
		bottom: rem(-120);
		right: 0;
		width: rem(380);
		height: rem(743);
		z-index: 2;
		background: url(../img/section5/right_bg.png) no-repeat;

		@include min1050 {
			bottom: rem(20);
			width: rem(119);
			height: rem(223);
			background: url(../img/section5/right_bg_mobile.png) no-repeat;
		}

		@include min540 {
			bottom: auto;
			top: rem(124);
		}

	}

	>.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;

		@include min1050 {
			justify-content: center;
		}
	}

	&__left {
		margin-left: rem(100);

		@include min1050 {
			margin-left: 0;

			order: 9999;
		}
	}

	&__right {
		position: absolute;
		top: rem(35);
		right: rem(32);

		@include min1200 {
			top: rem(68);
			right: rem(-150);
		}

		@include min1050 {
			position: relative;
			top: rem(42);
			right: auto;
			margin-left: rem(40);
			order: -9999;
		}

		@include min768 {
			margin-left: rem(10);
		}

		@include min540 {
			top: rem(28);
		}

		img {
			@include min1200 {
				width: 70%;
			}

			@include min768 {
				width: 80%;
			}
		}
	}
}