.investment {
	margin-bottom: rem(228);

	@include min768 {
		margin-bottom: rem(42);
	}

	&__top {
		margin-bottom: rem(50);

		.sect-title-40 {
			margin-bottom: rem(48);

			@include min540 {
				margin-bottom: rem(24);
			}
		}

		.sect-text {
			margin-bottom: rem(28);

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;

		&_left {
			display: flex;
			flex-direction: column;
			flex: 0 0 rem(672);
			max-width: rem(672);
			margin-right: rem(80);

			margin-top: rem(24);

			@include min1200 {
				flex: 0 0 50%;
				max-width: 50%;
			}

			@include min768 {
				flex: 0 0 100%;
				max-width: 100%;
				order: 9999;
				margin-right: 0;
			}

			.item {
				display: flex;
				// flex-wrap: wrap;
				padding-top: rem(18);
				padding-bottom: rem(72);

				border-top: rem(1) solid #EEEEFA;

				@include min1200 {
					flex-wrap: wrap;
					padding-top: rem(20);
					padding-bottom: rem(30);
				}

				&__left {
					flex: 0 0 rem(296);
					max-width: rem(296);

					padding-top: rem(10);

					font-size: rem(12);
					color: #8E8E93;
					line-height: 140%;
					letter-spacing: rem(1);

					text-transform: uppercase;

					@include min1200 {
						flex: 0 0 100%;
						max-width: 100%;


					}
				}

				&__right {
					text-transform: none;
				}
			}
		}

		&_right {
			flex: 0 0 rem(585);
			max-width: rem(585);

			margin-right: rem(-200);

			@include min1050 {
				flex: 0 0 50%;
				max-width: 50%;
			}

			@include min768 {
				flex: 0 0 calc(100% + 64px);
				max-width: calc(100% + 64px);
				margin-bottom: rem(24);
				// margin-right: rem(-32);
				// margin-left: rem(-64);
				order: -9999;

				position: relative;
				right: 0;
			}

			img {
				width: 100%;
				height: rem(355);
				object-fit: cover;
				display: block;

				@include min540 {
					height: rem(200);
				}
			}
		}
	}
}