.tab_info {
	position: relative;
	height: rem(715);
	background-repeat: no-repeat;
	background-position: top center;
	background-size: cover;

	margin-bottom: rem(270);

	transition: var(--transition);

	@include min1050 {
		height: auto;
	}

	@include min768 {
		// height: rem(792);
		margin-bottom: rem(128);
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(40, 40, 46, .8);
	}

	> .container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		position: relative;
		z-index: 1;

		@include min1050 {
			justify-content: center;
		}
	}

	&__item {
		display: none;
		flex-direction: column;

		padding: rem(100);

		box-shadow: 0px 22px 39px -26px rgba(199, 211, 217, 0.5);


		@include min1050 {
			padding: 0;
			align-items: center;
		}

		.image {
			position: relative;
			width: rem(144);
			height: rem(144);
			border-radius: 50%;
			background-color: #F7F6F3;
			margin-bottom: rem(60);
			margin-top: rem(46);

			@include min768 {
				width: rem(80);
				height: rem(80);
				margin-top: rem(40);
				margin-bottom: rem(24);
			}

			svg {
				position: absolute;
				top: rem(-28);
				right: rem(-14);
			}
		}

		.sect-title-28 {
			margin-bottom: rem(16);
			max-width: rem(300);

			@include min1050 {
				max-width: 100%;
				padding-left: rem(32);
				padding-right: rem(32);
			}

			@include min768 {
				text-align: center;
			}
		}

		.sect-text {
			margin-bottom: rem(60);

			@include min1050 {
				padding-left: rem(32);
				padding-right: rem(32);
			}

			@include min768 {
				margin-bottom: rem(40);
			}
		}

		.button-accent {
			align-self: flex-start;

			@include min1050 {
				width: 100%;
			}
		}
	}

	&__tab1 {
		.image {
			svg {
				width: rem(96);

				@include min768 {
					width: rem(53);
				}
			}
		}
	}

	&__tab2 {
		.image {
			svg {
				top: rem(-4);
				right: rem(-35);

				width: rem(117);

				@include min768 {
					right: rem(-20);
					top: rem(-8);
					width: rem(65);
				}
			}
		}
	}

	&__tab3 {
		.image {
			svg {
				width: rem(84);
				top: rem(-15);
				right: rem(-14);

				@include min768 {
					right: rem(-6);
					top: rem(-8);
					width: rem(46);
				}
			}
		}
	}

	&__tab4 {
		.image {
			svg {
				top: rem(-4);
				right: rem(-35);

				width: rem(104);

				@include min768 {
					right: rem(-18);
					top: rem(-1);
					width: rem(57);
				}
			}
		}
	}

	&__tab5 {
		.image {
			svg {
				top: rem(4);
				right: rem(-5.5);

				width: rem(86);

				@include min768 {
					right: rem(-6);
					top: 0;
					width: rem(48);
				}
			}
		}
	}

	&__item.active {
		display: flex;
	}

	&__left {
		background-color: white;
		width: rem(630);
		margin-top: rem(80);

		display: flex;
		flex-direction: column;

		@include min1200 {
			width: rem(520);
		}

		@include min1050 {
			margin-top: rem(50);
			position: relative;
			bottom: rem(-30);
			width: rem(400);

			order: 9999;
		}

	}

	&__right {
		margin-left: rem(140);
		position: absolute;
		top: rem(190);
		right: rem(128);
		width: rem(310);
		height: rem(368);

		@include min1050 {
			position: relative;
			margin-left: auto;
			margin-right: auto;
			margin-top: rem(80);
			top: auto;
			right: auto;

			width: 100%;
			height: auto;

			order: -9999;
		}


		ul {
			display: flex;
			flex-direction: column;

			@include min1050 {
				align-items: center;
			}

			li {
				margin-bottom: rem(48);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-size: rem(18);
					line-height: 140%;
					color: white;
					text-transform: uppercase;

					display: flex;
					align-items: center;

					white-space: nowrap;

					svg {
						width: 0;
						opacity: 0;
						margin-right: 0;
						transition: var(--transition);
					}

					&:hover {
						color: var(--c-accent);

						svg {
							width: rem(24);
							opacity: 1;
							margin-right: rem(28);
						}
					}
				}

				a.active {
					color: var(--c-accent);

					svg {
						flex: 0 0 rem(24);
						width: rem(24);
						opacity: 1;
						margin-right: rem(28);
					}
				}
			}
		}
	}
}