.record_form {
	margin-bottom: rem(310);
	position: relative;

	@include min768 {
		margin-bottom: rem(368);
	}

	.container {
		position: relative;
	}

	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		top: rem(-157);
		left: 0;
		width: rem(910);
		height: rem(1154);
		background: url(../img/section9/left_bg.png) no-repeat;
		background-size: 100%;

		@include min768 {
			top: rem(-240);
			width: rem(264);
			height: rem(304);
			background: url(../img/section9/left_bg_mobile.png) no-repeat;
			background-size: 100%;
		}
	}

	&__left {
		.button-accent {
			@include min540 {
				padding-left: rem(34);
				padding-right: rem(34);
			}

			@include min350 {
				padding-left: rem(16);
				padding-right: rem(16);
			}
		}
	}

	&__right {
		position: absolute;
		top: rem(-70);
		right: rem(-200);
		z-index: -1;

		height: rem(810);

		@include min768 {
			top: auto;
			bottom: rem(-284);
			height: rem(654);
			right: rem(-32);
			left: rem(-32);
		}

		@include min540 {
			height: rem(327);
		}

		img {
			width: 100%;
			height: rem(840);
			display: block;
			object-fit: cover;

			@include min768 {
				height: rem(654);
			}

			@include min540 {
				height: rem(327);
			}
		}
	}
}