.form_message {
	background-color: var(--c-text-black);
	padding: rem(70) rem(65);

	width: rem(506);

	@include min540 {
		width: 100%;
		padding: rem(48) rem(28) rem(40) rem(28);
	}

	.sect-title-24 {
		color: white;
		margin-bottom: rem(16);

		@include min540 {
			margin-bottom: rem(12);
		}
	}

	.sect-text {
		margin-bottom: rem(74);

		@include min540 {
			margin-bottom: rem(40);
		}
	}

	.field-set {
		margin-bottom: rem(28);

		@include min540 {
			margin-bottom: rem(14);
		}
	}

	.field-text {
		margin-bottom: rem(48);

		@include min540 {
			margin-bottom: rem(32);
			margin-top: rem(20);
		}
	}

	.button-accent {
		width: 100%;

		@include min350 {
			padding: rem(25) rem(22);
		}
	}
	
}

.field-set {
	position: relative;
	

	.icon {
		position: absolute;
		bottom: rem(14);
		left: 0;

		@include min540 {
			bottom: rem(7);
		}
	}

	.error-message {
		position: absolute;
		bottom: rem(-24);
		left: 0;
		color: #9D4A4A;
		
		@include min540 {
			font-size: rem(12);
			bottom: rem(-16);
		}
	}

	.field {
		padding: rem(16) rem(10) rem(16) rem(45);

		border-bottom: rem(1) solid rgba(255,255,255,.2);
		background-color: transparent;
		width: 100%;
		color: white;
		font-size: rem(16);

		@include placeholder {
			color: #7D7D80;
		}

		@include min540 {
			padding: rem(12) rem(10) rem(12) rem(32);
			font-size: rem(14);
		}
	}
}
.field-set.error {
	.field {
		border-color: #9D4A4A !important;
	}
}

.field-text {
	p {
		color: rgba(185,185,185,.5);
		font-size: rem(12);
		line-height: 140%;

		a {
			text-decoration: underline;
		}
	}
}